<template>
  <div class="card-bookmark col-3">
    <div class="card mx-auto my-3">
      <img :src="bookmark.Movie.imgUrl" class="card-img-top" height="250px" />
      <div class="card-body card-body">
        <h6 class="card-title">Title: {{ bookmark.Movie.title }}</h6>
        <p class="card-text">Genre: {{ bookmark.Movie.Genre.name }}</p>
        <p class="card-text">Author: {{ bookmark.Movie.User.username }}</p>
        <p class="card-text">Rating: {{ bookmark.Movie.rating }}</p>
      </div>
      <a
        href="#"
        class="btn btn-danger text-center text-light mx-3 my-2"
        @click.prevent="removeBookmark"
        >Remove Bookmark</a
      >
      <a
        href="#"
        class="btn btn-info text-center text-light mx-3 my-2"
        @click.prevent="detailBookmark"
        @click="todetailBookmark"
        >Detail</a
      >
    </div>
  </div>
</template>

<script>
export default {
  name: "BookmarkCard",
  props: ["bookmark"],
  methods: {
    async removeBookmark() {
      await this.$store.dispatch("removeBookmark", this.bookmark.id);
    },
    async detailBookmark() {
      await this.$store.dispatch("detailBookmark", this.bookmark.id);
    },
    todetailBookmark() {
      this.$router.push("/bookmark/detail");
    },
  },
};
</script>

<style scoped>
.card {
  background: #293b5f;
  justify-content: center;
}
.card-bookmark {
  margin-left: 3vw;
}
</style>
